.rating-filter {
    margin: 10px 0;

    >div {
        width: calc(100% / 3);
    }

    .cats {
        li {
            margin: 0px;
            position: relative;

            a {
                padding: 5px 10px;
                font: 500 11px/150% var(--font2);
                letter-spacing: 1px;
                text-transform: uppercase;
                color: var(--color2);
                opacity: .8;
            }

            &:hover {
                a {
                    opacity: 1;
                }
            }

            &.active {
                background: var(--color3);
            }
        }
    }
}


.grid-list {
    width: 100%;

    .grid-list-item {
        width: calc(100% / 6 - 2px) !important;
        height: auto;

        &:before {
            content: "";
            width: 1px;
            margin-left: -1px;
            float: left;
            height: 0;
            padding-top: 100%;
        }

        &:after {
            content: "";
            display: table;
            clear: both;
        }
    }
}